.my-active {
    color: $clr2;
}

.flex{
    display: flex;
    &.space-between{
        justify-content: space-between;
    }
    &.justify-center{
        justify-content: center;
    }
    &.align-center{
        align-items: center;
    }
    &.column {
        flex-direction: column;
    }
}

